import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from '../service/user.service'; 
import { User } from '../model/User'; 

@Component({
  selector: 'app-upload-profile-pic-route',
  templateUrl: './upload-profile-pic-route.component.html',
  styleUrls: ['./upload-profile-pic-route.component.scss']
})
export class UploadProfilePicRouteComponent implements OnInit {

  $auth: User; 
  $profile_pic: string;

  constructor(public _route: Router, public _user: UserService) { }

  ngOnInit() {
    this._user.authUser().subscribe(
      user => {
        this.$auth = user; 
        this.$profile_pic = user.profile_pic;
      }
    )
  }
  onStatusChange(e){
    if(e){
      this._route.navigate(['/settings']); 
    }
  }
}
