import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { UserService } from '../service/user.service'; 
import { HttpEventType } from '@angular/common/http'; 
import { CommonMessageService } from '../service/common-message.service'; 

@Component({
  selector: 'app-upload-profile-pic',
  templateUrl: './upload-profile-pic.component.html',
  styleUrls: ['./upload-profile-pic.component.scss']
})
export class UploadProfilePicComponent implements OnInit {
  constructor(
    public _user: UserService,
    public _message: CommonMessageService
  ) { }

  @Output() status = new EventEmitter(); 
  @Input() profile_pic = 'http://clinic.com/img/avatar.jpg'; 

  loading = false; 
  progress = 0; 

  ngOnInit() {
  }
  fileSelected(file){
    this.loading = true;
    const img = file.target.files; 
    if (img && img[0]) {
      var reader = new FileReader();

      reader.readAsDataURL(img[0]);

      reader.onload = (f) => {
        // @ts-ignore
        this.profile_pic = f.target.result;
      }
      const uploadedImage = new FormData(); 
      uploadedImage.append('image', img[0], img[0].name); 
      this._user.uploadProgilePic(uploadedImage).subscribe(
        event => {
        
          if(event.type == HttpEventType.UploadProgress){
            this.progress = Math.round(event.loaded/event.total * 100);
          }else if(event.type == HttpEventType.Sent){
            this.loading = false; 
            this._message.httpSuccess("Uploaded your profile picture");
            this.status.emit('true');
            console.log("emited");  
          }
        }, 
        error => {
         this._message.httpError(error); 
        }
      )
    } 
  }
}
