import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material'; 
import { User } from './model/User'; 
import { UserService } from './service/user.service'; 
import { FirstTimeLoginComponent } from './first-time-login/first-time-login.component'; 

declare var M; 

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})

 
export class AppComponent {
  title = 'admin2';

  profilePic: string;
  $auth: User; 

  modal; 
  constructor(public _modal: MatDialog, public _user: UserService) { 
    this.laodAuthUser(); 
  }

  ngOnInit(){
    document.addEventListener('DOMContentLoaded', function() {
      M.AutoInit(); 
    });
   
    this._user.authUser().subscribe(
      user => {
        if(user.isFirstTime){
          this.modal = this._modal.open(FirstTimeLoginComponent, {
            width: '500px',
            height: '280px', 
            disableClose: true, 
            data: {
              $auth: user,
              $profilePic: this.profilePic
            }
          }); 
          this.modal.afterClosed().subscribe(
            finised => {
              if(finised){
                this.laodAuthUser(); 
              } 
            }
          )
        }
      }
    )  
  }
  laodAuthUser(){
    this._user.authUser().subscribe(
      user => {
        this.$auth = user; 
        this.profilePic = user.profile_pic; 
      }
    )
  }

}
